.footer-container{
  background-color: #121441;
  padding: 4rem 0 2rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.footer-subscription{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-bottom: 24px;
  padding: 24px;
  color: #fff;

}

.footer-subscription  p {
  font-family: Muli, sans-serif;
  width: 75%;



}

.footer-subscription-heading{
  margin-bottom: 24px;
  font-size: 28px;
  line-height: 38px;
  letter-spacing: 1px;
  font-weight: bold;

}

.footer-subscription-text{
  margin-bottom: 24px;
  font-size: 20px;
}

.footer-links{
  width: 100%;
  max-width: 1000px;
  display: flex;
  justify-content: left;
}

.footer-links-wrapper{
  display: flex;
}

.footer-links-items{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 16px;
  text-align: left;
  width: 160px;
  box-sizing: border-box;

}
.footer-links-items h2 {
  margin-bottom: 16px;
}
.footer-links-items  h2{
  text-transform: uppercase;
  font-size: 1rem;
  line-height: 32px;
  font-weight: 600;
  letter-spacing: .18em;
  color: #fff;
}

.footer-links-items a {
  color: #fff;
  text-transform: capitalize;
  text-decoration: none;
  margin-bottom: 0.5rem;
}

.footer-links-items a:hover {
  color: #e9e9e9;
  transition: 0.3s ease-out;
}

.social-icon-link {
  color: #fff;
  font-size: 24px;
}

.social-media {
  max-width: 1000px;
  width: 100%;
}

.social-media-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  max-width: 1000px;
  margin: 40px auto 0 auto;
}

.social-icons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 240px;
}

.social-logo {
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
  display: flex;
  align-items: center;
  margin-bottom: 16px;
}
.social-logo img{
  height: 50px;
  width: 50px;

}
.website-rights {
  color: #fff;
  margin-bottom: 16px;
}

@media screen and (max-width: 820px) {
  .footer-links {
    padding-top: 2rem;
  }


  .btn {
    width: 100%;
  }

  .footer-links-wrapper {
    flex-direction: column;
  }

  .social-media-wrap {
    flex-direction: column;
  }
}

